	module.exports = {"data":{"markdownRemark":{"html":"<h1>Split Method</h1>\n<p><code>String.Split</code> method can parse a string: As input, Split takes an characters that indicate which characters is the separator generating an array of the sub strings.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>string myText = \"I like pizza\";\n\n// Split the string by ' '(space) character.\nstring[] splitResoult = myText.Split(' ');\n\n// The array splitResoult, now contains 3 substrings.\n\n// Now print the array of substrings\nforeach(string x in splitResoult)\n{\n    // Write On Console\n    Console.WriteLine(x);\n}</code></pre>\n      </div>\n<h2>Output:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>> I\n> like\n> pizza</code></pre>\n      </div>","fields":{"slug":"/csharp/split/"},"frontmatter":{"title":"Split"}}},"pathContext":{"slug":"/csharp/split/"}}

